.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  width: 0px;
  height: 0px;
  opacity: 0;
}

.slider {
  position: absolute;
  cursor: pointer;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  background-color: #ccc;
  transition: 0.4s;
}
.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: 0.4s;
}
input:checked + .slider {
  background-color: #2196f3;
}
input:checked + .slider:before {
  transform: translateX(26px);
}
 .slider.rounded {
  border-radius: 34px;
 }
 .slider.rounded:before {
  border-radius: 50%
 }

 .flex {
  display: flex;
  justify-content: center;
  align-items: center;
 }

 .switch_name {
  font-size: 1.4rem;
  margin-right: 20px;
 }